import React, { Component } from 'react';
import { Form, Text, Checkbox, RadioGroup, Radio } from 'react-form'
import axios from 'axios'
import querystring from 'querystring';
import ReactModal from 'react-modal';

import './App.css';

class App extends Component {

  constructor () {
    super();
    this.state = {
      showModal: false
    };

    this.handleOpenModal = this.handleOpenModal.bind(this);
    this.handleCloseModal = this.handleCloseModal.bind(this);
  }

  handleOpenModal () {
    this.setState({ showModal: true });
  }

  handleCloseModal () {
    this.setState({ showModal: false });
  }

  convertToYesNo(bool) {
    if (bool === true) {
      return "Kyllä";
    } else {
      return "Ei";
    }
  }

  render() {
    return (

        <div className="flex-container">

          <div className="flex-item">
            <h1>
              <img src="./img/Tilaa-Skholen-uutiskirje.svg" alt="Tilaa Skholen uutiskirje" />
            </h1>
            <h2>Mitä uutta hoitoalan koulutuksessa? Miten hyödynnän työssäni uusinta tietoa ammatillisen osaamisen kehittämisestä?</h2>
            <hr width="33%" />
            <p>Skholen uutiskirjeen tilaajana saat valikoituja otteita verkon hyödyllisimmistä sisällöistä koskien hoitoalan koulutusta, ammatillisen osaamisen kehittämistä sekä oppimisen haasteita ja ratkaisuja. Kirje saapuu sähköpostiisi kahden viikon välein.</p>
            <p>Mikäli haluat jossain vaiheessa perua uutiskirjeen tilauksen, sen voit tehdä jokaisen uutiskirjeen lopusta löytyvästä linkistä.</p>
          </div>

          <div className="flex-item">
            <p>Kentät: * = pakollinen</p>
            <Form
              onSubmit={(values) => {
                  values.oppil = this.convertToYesNo(values.oppil);
                  values.yksit = this.convertToYesNo(values.yksit);
                  values.julki = this.convertToYesNo(values.julki);

                  axios.get('https://wt-23776c021989c4b333e64635c2fcad17-0.run.webtask.io/wt-newsletter/?' + querystring.stringify(
                  {
                    email: values.email,
                    fname: values.fname,
                    lname: values.lname,
                    oppil: values.oppil,
                    yksit: values.yksit,
                    julki: values.julki,
                    skhole: values.skhole,
                  }))
                .then(function (response) {
                  this.handleOpenModal();
                  //console.log(response);
                })
                .catch(function (error) {
                  //console.log(error);
                });
                this.handleOpenModal();
                //console.log('Success posting', values)
              }}
              validate={values => {
                const { fname, lname, email, skhole } = values
                return {
                  fname: !fname ? 'Etunimi vaaditaan' : undefined,
                  lname: !lname ? 'Sukunimi vaaditaan' : undefined,
                  email: !email ? 'Sähköposti vaaditaan' : undefined,
                  skhole: !skhole ? 'Organisaatiovalinta vaaditaan' : undefined
                }
              }}
            >
              {({submitForm}) => {
                return (
                  <form onSubmit={submitForm}>
                    Etunimi*<br />
                    <Text className='input-text' field='fname' />
                    Sukunimi*<br />
                    <Text className='input-text' field='lname' />
                    Sähköpostiosoite*<br />
                    <Text className='input-text' field='email' />
                    Minkätyyppisen organisaation näkökulmasta hoitoalan koulutukseen olet pääasiallisesti kiinnostunut? Voit valita useamman.<br />
                    <br /><Checkbox className='input-checkbox' field='oppil' /> Oppilaitokset<br />
                    <Checkbox className='input-checkbox' field='yksit' /> Yksityiset yritykset<br />
                    <Checkbox className='input-checkbox' field='julki' /> Julkiset terveyspalvelut
                    <br />
                    <br />
                    Organisaationi käyttää Skholea<br />
                    <br />
                    <RadioGroup field="skhole">
                      <label>
                        <Radio value='Kyllä' />
                        <span>Kyllä</span>
                      </label>
                      <label>
                        <Radio value='Ei' />
                        <span>Ei</span>
                      </label>
                      <label>
                        <Radio value='En tiedä' />
                        <span>En tiedä</span>
                      </label>
                    </RadioGroup>

                    <button type="submit">Tilaa uutiskirje</button>
                      <ReactModal
                         isOpen={this.state.showModal}
                         contentLabel="Minimal Modal Example"
                         aria={{
                          labelledby: "heading",
                          describedby: "full_description"
                        }}
                      >
                        {/*<button onClick={this.handleCloseModal}>Sulje</button>*/ }
                          <h1 id="heading-subscribed">Kiitos uutiskirjeen tilauksesta!</h1>
                          {/*<div id="full_description">
                            <p>Jotain muuta tähän?</p>
                          </div>*/ }
                      </ReactModal>
                  </form>
                )
              }}
            </Form>

          </div>

        </div>

    );
  }
}

export default App;



